export default {
  name: 'RealTimeData',
  props: {
    environmentData: {
      type: Array,
      default: () => [
        { label: 'PM2.5', value: '35 μg/m³' },
        { label: '温度', value: '25°C' },
        { label: '湿度', value: '60%' }
      ]
    },
    energyData: {
      type: Array,
      default: () => [
        { label: '电力消耗', value: '120 kWh' },
        { label: '水消耗', value: '15 m³' },
        { label: '燃气消耗', value: '8 m³' }
      ]
    },
    weatherData: {
      type: Array,
      default: () => [
        { label: '今日天气', value: '晴' },
        { label: '风速', value: '5 km/h' },
        { label: '降水概率', value: '10%' }
      ]
    },
    warningData: {
      type: Array,
      default: () => [
        { label: '安全隐患', value: '2条' },
        { label: '进度延误', value: '1项' },
        { label: '质量问题', value: '0项' }
      ]
    }
  },
  mounted() {
    this.loadAMap().then(() => {
      this.initMap();
    });
  },
  methods: {
    loadAMap() {
      return new Promise((resolve, reject) => {
        if (typeof AMap !== 'undefined') {
          resolve(); // 如果 AMap 已经加载，直接返回
        } else {
          const script = document.createElement('script');
          script.src = 'https://webapi.amap.com/maps?v=2.0&key=28c128938daff70e889551a0f56160c7';
          script.onload = () => resolve();
          script.onerror = (err) => reject(err);
          document.head.appendChild(script);
        }
      });
    },
    initMap() {
      const map = new AMap.Map('map', {
        zoom: 15,
        center: [116.397428, 39.90923], // 默认中心点为北京天安门
        viewMode: '2D'
      });

      // 添加控件（如缩放、定位等）
      AMap.plugin(['AMap.ToolBar', 'AMap.Scale'], () => {
        map.addControl(new AMap.ToolBar());
        map.addControl(new AMap.Scale());
      });
    }
  }
};
